import { createAsyncThunk } from "@reduxjs/toolkit";
import { getParcelInfo } from "../services/novaApi";

export const getTtnInfo = createAsyncThunk(
  "parcel/get",
  async (data, thunk) => {
    try {
      const res = await getParcelInfo(data);
      if (!res.success) {
        throw new Error();
      }

      return res;
    } catch (error) {
      return thunk.rejectWithValue(error);
    }
  }
);
